.visually-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}

.advantages {
  padding: 42px 0 60px 0;
  background-color: #84a178;

  @include tablet {
    padding: 48px 0 204px 0;

    &-list {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }

  @include desktop {
    padding: 60px 0 180px 0;
  }

  &-container {
    padding-left: 45px;
    padding-right: 45px;

    @include tablet {
      padding-left: 76px;
      padding-right: 76px;
    }

    @include desktop {
      padding-left: 16px;
      padding-right: 16px;
    }
  }

  &-list__item {
    text-align: center;

    @include tablet {
      width: 152px;
      width: calc((100% - 2 * 80px) / 3);
      margin-right: 80px;
      margin-top: 0;
    }

    @include desktop {
      width: 368px;
      width: calc((100% - 2 * 32px) / 3);
      margin-right: 32px;
      margin-top: 0;
    }
  }

  @media screen and (max-width: 767px) {
    &-list__item + &-list__item {
      margin-top: 28px;
    }
  }


  // @include tablet {
  //   &-list__item:nth-child(3n) {
  //     // margin-right: 0;
  //   }
  // }
  @include tablet {
    &-list__item:nth-child(3n) {
      margin-right: 0;
    }
  }

  &-list__item:nth-child(1)::before {
    content: '';
    display: block;
    width: 50px;
    height: 50px;
    background-color: $how-its-made-yellow1;
    border-radius: 50%;
    margin: 0 auto;
    background-image: url(../images/advantages/advantages-img1-mob-1x.png);
    background-size: 36px;
    background-repeat: no-repeat;
    background-position: center;

    @media (min-device-pixel-ratio: 2),
      (-webkit-min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/advantages/advantages-img1-mob-2x.png);
    }
  }

  &-list__item:nth-child(2)::before {
    content: '';
    display: block;
    width: 50px;
    height: 50px;
    background-color: $how-its-made-yellow1;
    border-radius: 50%;
    margin: 0 auto;
    background-image: url(../images/advantages/advantages-img2-mob-1x.png);
    background-size: 36px;
    background-repeat: no-repeat;
    background-position: center;

    @media (min-device-pixel-ratio: 2),
      (-webkit-min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/advantages/advantages-img2-mob-2x.png);
    }
  }

  &-list__item:nth-child(3)::before {
    content: '';
    display: block;
    width: 50px;
    height: 50px;
    background-color: $how-its-made-yellow1;
    border-radius: 50%;
    margin: 0 auto;
    background-image: url(../images/advantages/advantages-img3-mob-1x.png);
    background-size: 36px;
    background-repeat: no-repeat;
    background-position: center;

    @media (min-device-pixel-ratio: 2),
      (-webkit-min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/advantages/advantages-img3-mob-2x.png);
    }
  }

  &-list__title {
    font-family: $third-font;
    font-style: normal;
    font-weight: 700;
    font-size: 36px;
    line-height: 1.17;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $white-color;
    display: inline-block;
    margin-top: 16px;

    @include tablet {
      font-size: 48px;
      line-height: 1.23;
      margin-top: 32px;
    }

    @include desktop {
      font-size: 58px;
      line-height: 1.22;
      margin-top: 40px;
    }
  }

  &-list__text {
    font-weight: 700;
    font-size: 12px;
    line-height: 1.7;
    color: $white-color;
    margin-top: 18px;

    @include tablet {
      font-weight: 400;
      font-size: 14px;
      line-height: 1.74;
      margin-top: 16px;
    }

    @include desktop {
      font-size: 16px;
      line-height: 1.76;
      padding: 0 54px;
    }
  }
}
